<template>
  <div>
    <input type="text" v-model="msg" @keyup.enter="send">
    <span v-if="closed">连接中...</span>
    <span v-else>已连接</span>
    <div>
      <ul>
        <li v-for="item in list">
          {{item.time}}=>{{item.msg}}
        </li>
      </ul>
    </div>

  </div>
</template>
<script>
import formatTime from '@/filters/formatTime'
export default {
  data() {
    return {
      url: `ws://lp-ws.herokuapp.com/${this.$route.params.id}`,
      // url: `ws://localhost:3000/${this.$route.params.id}`,
      ws: null,
      msg: null,
      list: [],
      closed: true,
      error: false,
      timeout: 5000,
      timeoutObj: null,
      serverTimeoutObj: null
    }
  },
  mounted() {
    this.connect()
  },
  methods: {
    resetHert() {
      clearTimeout(this.timeoutObj)
      clearTimeout(this.serverTimeoutObj)
      this.startHert()
    },
    startHert: function() {
      var _this = this
      this.timeoutObj = setTimeout(function() {
        _this.ws.send(JSON.stringify({ check: true, msg: 'check' }))
        _this.serverTimeoutObj = setTimeout(function() {
          _this.ws.close()
          _this.connect()
        }, _this.timeout)
      }, _this.timeout)
    },
    connect() {
      this.ws = this.createWS(this.url)
      this.ws.onopen = () => {
        this.closed = false
        this.startHert()
      }
      this.ws.onmessage = msg => {
        this.resetHert()
        try {
          msg = JSON.parse(msg.data)
        } catch (err) {
          msg = msg.data
        }
        if (!msg.check) {
          this.list.push({
            type: 1,
            msg: msg,
            time: formatTime(new Date())
          })
        }
      }
      this.ws.onclose = () => {
        if (this.error) {
          setTimeout(() => {
            this.connect()
          }, this.timeout)
        } else {
          this.connect()
        }
        this.closed = true
        this.error = false
      }
      this.ws.onerror = () => {
        this.error = true
      }
    },
    createWS(urlValue) {
      if (window.WebSocket) return new WebSocket(urlValue)
      if (window.MozWebSocket) return new MozWebSocket(urlValue)
      return false
    },
    send() {
      this.list.push({
        type: 0,
        msg: this.msg,
        time: formatTime(new Date())
      })
      this.ws.send(this.msg)
      this.msg = null
    }
  }
}
</script>



